<template>
  <div>
    <div class="form_box">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <template>
          <div class="change_box">
            <div class="click_box">
              <el-button
                type="primary"
                style="margin-top: 12px; font-size: 14px"
                @click="next_step(ruleForm)"
                size="small"
                >,</el-button
              >
            </div>
            <el-form-item size="small" label="" prop="fenlei">
              <el-cascader
                size="small"
                v-model="ruleForm.fenlei"
                :options="options"
                :props="{ value: 'id', label: 'name' }"
                placeholder=""
              >
              </el-cascader>
            </el-form-item>

            <el-form-item
              size="small"
              label=""
              prop="name"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>

            <el-form-item
              size="small"
              label=""
              prop="name2"
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.name2"></el-input>
            </el-form-item>

            <el-form-item size="small" label="" prop="pinpai">
              <el-select v-model="ruleForm.pinpai" placeholder="">
                <el-option
                  v-for="item in brandList"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </el-form-item>

            <el-form-item size="small" label="" style="width: 700px">
              <el-input
                type="textarea"
                v-model="ruleForm.desc"
                placeholder=""
              ></el-input>
            </el-form-item>

            <el-form-item
              size="small"
              label=""
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.name"></el-input>
            </el-form-item>

            <el-form-item
              size="small"
              label=""
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.price"></el-input>
            </el-form-item>
            <el-form-item
              size="small"
              label=""
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.markPrice"></el-input>
            </el-form-item>
            <el-form-item
              size="small"
              label=""
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.kucun"></el-input>
            </el-form-item>

            <el-form-item
              size="small"
              label=""
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.danwei"></el-input>
            </el-form-item>

            <el-form-item size="small" label="" width="100px">
              <el-input
                v-model="ruleForm.weight"
                style="width: 300px"
              ></el-input>
              &nbsp;&nbsp;&nbsp;
            </el-form-item>

            <el-form-item
              size="small"
              label=""
              width="100px"
              style="width: 700px"
            >
              <el-input v-model="ruleForm.paixu"></el-input>
            </el-form-item>
          </div>
        </template>
      </el-form>
    </div>
  </div>
</template>

<script>
import { getPmsCateWithChildren } from "../../../../../api/productcate.js";
import { getBrandList } from "../../../../../api/productbrand.js";
export default {
  props: ["vs"],
  data() {
    return {
      info_step: 1,
      options: [],
      brandList: [],
      ruleForm: {
        name: "",
        name2: "",
        fenlei: "",
        pinpai: "",
        desc: "",
        price: 0,
        markPrice: 0,
        kucun: 0,
        danwei: "",
        weight: 0,
        paixu: 0,
      },
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 10, message: " 3  5 ", trigger: "blur" },
        ],
        name2: [
          { required: true, message: "", trigger: "blur" },
          { min: 1, max: 10, message: " 3  5 ", trigger: "blur" },
        ],
        fenlei: [
          { required: true, message: "", trigger: "change" },
        ],
        pinpai: [
          { required: true, message: "", trigger: "change" },
        ],
      },
    };
  },
  methods: {
    next_step(ruleForm) {
      console.log(this.$refs);
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.$emit("trans_infostep", this.info_step);
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
  },
  mounted() {
    getPmsCateWithChildren().then((data) => {
      // console.log("", data);
      this.options = data.data;
    });

    //
    getBrandList().then((data) => {
      // console.log("branddata", data);
      this.brandList = data.data;
    });
  },
};
</script>
<style scoped='scoped'>
.danwei {
  outline: 1px solid red;
  width: 20px;
}
.click_box {
  position: absolute;
  top: 840px;
  left: 860px;
}
.form_box {
  padding-top: 20px;
  width: 100%;
  height: 630px;
  float: left;
}
</style>
