<template>
  <div>
    <div class="search">
      <div class="bt_search_head">
        <div style="font-size: 16px">
          <i class="el-icon-search"></i>
          
        </div>
        <div>
          <el-button size="small" @click="resetSearch"></el-button>
          <el-button size="small" type="primary" @click="seachHandler"
            ></el-button
          >
        </div>
      </div>
      <el-form :inline="true" :model="queryparams">
        <div class="bt_form_br">
          <el-form-item label=":">
            <el-input
              style="width: 208px"
              size="small"
              @keydown.enter.native="seachHandler"
              v-model="queryparams.keyword"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-input
              style="width: 208px"
              size="small"
              v-model="queryparams.productSn"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-cascader
              size="small"
              v-model="value"
              :options="options"
              :props="{ value: 'id', label: 'name' }"
              @change="changeCateHandler"
            >
            </el-cascader>
          </el-form-item>
        </div>
        <div class="bt_form_br">
          <el-form-item size="small" label=":">
            <!-- <el-input size="small" v-model="queryparams.keyword" placeholder=""></el-input> -->
            <el-select v-model="queryparams.brandId" placeholder="">
              <el-option
                v-for="item in brandList"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
          </el-form-item>
          
          <el-form-item size="small" label=":">
            <!-- <el-input v-model="queryparams.keyword" placeholder=""></el-input> -->
            <el-select v-model="queryparams.publishStatus" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="small" label=":">
            <!-- <el-input v-model="queryparams.keyword" placeholder=""></el-input> -->
            <el-select v-model="queryparams.verifyStatus" placeholder="">
              <el-option label="" value="1"></el-option>
              <el-option label="" value="0"></el-option>
            </el-select>
          </el-form-item>
        </div>
      </el-form>
    </div>
    <div class="datalist">
      <span class="dataliest-span1"
        ><i class="el-icon-tickets"></i> </span
      >
      <span class="dataliest-span2"
        ><el-button size="small" @click="add"></el-button></span
      >
    </div>
    <div class="biao">
      <el-table :data="tableData" border style="width: 100%" @selection-change="handleSelectionChange" ref="multipleTable">
        <el-table-column
          type="selection"
          width="55"
          align="center">
        </el-table-column>
        <el-table-column prop="id" align="center" label="" width="100">
        </el-table-column>
        <el-table-column label="" align="center" width="120">
          <template v-slot="vs">
            <img width="80" height="80" :src="vs.row.pic" />
          </template>
        </el-table-column>
        <el-table-column
          prop="name"
          align="center"
          label=""
          width="360"
        >
          <template v-slot="vs">
            <p>{{ vs.row.name }}</p>
            <p>{{ vs.row.brandName }}</p>
          </template>
        </el-table-column>
        <el-table-column prop="id" align="center" label="" width="129">
          <template v-slot="vs">
            <p>{{ vs.row.price }}</p>
            <p>{{ vs.row.productSn }}</p>
          </template>
        </el-table-column>
        <el-table-column align="center" label="" width="140">
          <template v-slot="vs">
            <p>
              
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="vs.row.publishStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handlepublishStatus(vs.row.id,vs.row.publishStatus)"
              >
              </el-switch>
            </p>
            <p>
              
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="vs.row.newStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handlenewStatus(vs.row.id,vs.row.newStatus)"
              >
              </el-switch>
            </p>
            <p>
              
              <el-switch
                :active-value='1'
                :inactive-value='0'
                v-model="vs.row.recommandStatus"
                active-color="#409eff"
                inactive-color="#dcdfe6"
                @change="handlerecommendStatus(vs.row.id,vs.row.recommandStatus)"
              >
              </el-switch>
            </p>
          </template>
        </el-table-column>
        <el-table-column prop="sort" align="center" label="" width="100">
        </el-table-column>
        <el-table-column align="center" label="" width="100">
          <template v-slot="vs">
            <el-button
              type="primary"
              icon="el-icon-edit"
              circle
              @click="getsku(vs.row)"
            ></el-button>
            <!---->
            <el-dialog
              :visible.sync="dialogVisible"
              width="40%"
              center
            >
              <span style='font-size:18px'></span>
              <p><p style='height:40px'></p>
                {{ dialog_productSn }}
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <!--Sku-->
                <el-input
                  style="width: 270px"
                  size="small"
                  @keydown.enter.native="searchSku(skudata[0].productId)"
                  v-model="skukeyword"
                  placeholder="sku"
                ></el-input>
                <el-button icon="el-icon-search" size="small" style='background-color:#f5f7fa' @click='searchSku(skudata[0].productId)'></el-button>
              </p>
              <!---->
              <el-table :data="skudata" border style="width: 100%;margin-top:20px">
                <el-table-column align="center" label="SKU" width="170">
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.skuCode'></el-input>
                  </template>
                </el-table-column>

                <el-table-column align="center" width="90" v-for='(item,index) in attributelist' :label='item.name'>
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.spData[index].value'></el-input>
                  </template>
                </el-table-column>

                <el-table-column align="center" label="" width="90">
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.price'></el-input>
                  </template>
                </el-table-column>
                <el-table-column  align="center" label="" width="91">
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.stock' align="center"></el-input>
                  </template>
                </el-table-column>
                <el-table-column align="center" label="" width="100"> <!--prop="lockStock"
                v-slot21-->
                  <template v-slot='vs'>
                    <el-input v-model='vs.row.lockStock'></el-input>
                  </template>
                </el-table-column>
              </el-table>
              <!--sku-->
              <span slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false;skukeyword=null"> </el-button>
                <el-button type="primary" @click='plEditSku(skudata[0].productId)'
                  > </el-button
                >
              </span>
            </el-dialog>
          </template>
        </el-table-column>
        <el-table-column prop="sale" align="center" label="" width="100">
        </el-table-column>
        <el-table-column align="center" label="" width="100">
          <template v-slot="vs">
            <p></p>
            <p>
              <a href="#" style="text-decoration: none; color: #66b1ff"
                ></a
              >
            </p>
          </template>
        </el-table-column>
        <el-table-column align="center" label="" width="180">
          <template v-slot="vs">
            <p>
              <el-button size="mini"></el-button
              ><el-button size="mini" @click='edit'></el-button>
            </p>
            <p class="gao"></p>
            <p>
              <el-button size="mini"></el-button
              ><el-button size="mini" type="danger" @click="dProduct(vs.row.id)"
                ></el-button
              >
            </p>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="fenye">
      <el-row>
        <el-col :span='14'>
          <el-select v-model="clickGetStatus" placeholder="" size='small'>
              <el-option
                v-for="item in plcaozuoData"
                :label="item.name"
                :value="item.value"
              ></el-option>
            </el-select>
            <el-button type='primary' size='small' @click='plOption'></el-button>
        </el-col>
        <el-col :span='10'>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="this.queryparams.pageNum"
            :page-sizes="[2, 3, 4, 5]"
            :page-size="5"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
          >
      </el-pagination></el-col>
      </el-row>
      
    </div>
  </div>
</template>

<script>
import { getShoplist } from "../../../../api/shop.js";
import { getPmsCateWithChildren } from "../../../../api/productcate.js";
import { getBrandList } from "../../../../api/productbrand.js";
import { productDelete } from "../../../../api/productdelete.js";
import { productSku } from "../../../../api/productsku.js";
import { updateSku } from "../../../../api/productsku.js";
import { changepublishStatus } from "../../../../api/shop.js";
import { changenewStatus } from "../../../../api/shop.js";
import { changerecommendStatus } from "../../../../api/shop.js";
import { productAttributelist } from "../../../../api/shop.js";
export default {
  data() {
    return {
      attributelist: [],
      isShow: false,
      title: "",
      search_skudata: [],
      skukeyword: null,
      clickGetStatus: [],
      idsArry: "",
      plcaozuoData: [
        { name: "", value: [1, 1] },
        { name: "", value: [0, 1] },
        { name: "", value: [1, 2] },
        { name: "", value: [0, 2] },
        { name: "", value: [1, 3] },
        { name: "", value: [0, 3] },
      ],
      dialog_productSn: "",
      spData: [],
      skudata: [],
      dialogVisible: false,
      total: 0,
      tableData: [],
      dtable: {},
      value: [],
      options: [],
      brandList: [], //
      // checked:'',
      queryparams: {
        pageNum: 1,
        pageSize: 5,
        brandId: null,
        keyword: "",
        productCategoryId: null, // id
        productSn: "", //
        publishStatus: null, // 0->1->
        verifyStatus: null, //0->1->
        // deleteStatus:null
      },
    };
  },

  methods: {
    //Sku
    searchSku(id) {
      productSku(id, this.skukeyword).then((resp) => {
        this.skudata = resp.data;
        this.skudata.forEach((element) => {
          element.spData = JSON.parse(element.spData);
        });
      });
    },

    plEditSku(id) {
      this.skukeyword = null;
      this.dialogVisible = false;
      // console.log('skuid',id)
      // console.log('skudata',this.skudata)
      this.skudata.forEach((element) => {
        element.spData = JSON.stringify(element.spData);
        // console.log("spData", element.spData);
      });
      console.log("skudata", this.skudata);
      updateSku(id, this.skudata);
    },

    //  
    plOption() {
      console.log("clickGetStatus", this.clickGetStatus);
      var status = this.clickGetStatus[0];
      var type = this.clickGetStatus[1];
      if (type == 1) {
        this.handlepublishStatus(this.idsArry, status); //
      } else if (type == 2) {
        this.handlerecommendStatus(this.idsArry, status);
      } else if (type == 3) {
        this.handlenewStatus(this.idsArry, status);
      }
    },
    //
    handlepublishStatus(id, status) {
      console.log(id, status);
      changepublishStatus({
        ids: id,
        publishStatus: status,
      }).then(() => {
        this.getslist();
      });
    },
    //
    handlenewStatus(id, status) {
      changenewStatus({
        ids: id,
        newStatus: status,
      }).then(() => {
        this.getslist();
      });
    },

    //
    handlerecommendStatus(id, status) {
      changerecommendStatus({
        ids: id,
        recommendStatus: status,
      }).then(() => {
        this.getslist();
      });
    },

    //elment change
    handleSelectionChange(val) {
      this.multipleSelection = val;
      console.log("val", val);
      var arry = val.map((element) => {
        return element.id;
      });
      console.log("arry", arry);
      this.idsArry = arry.join(",");
      console.log(this.idsArry);
    },

    getsku(row) {
      this.skukeyword='';
      this.spData = [];
      this.dialogVisible = true;
      this.dialog_productSn = row.productSn;
      console.log('productAttributeCategoryId',row.productAttributeCategoryId)

      // sku

        //
      productAttributelist({
        cid: row.productAttributeCategoryId,
        pageNum: null,
        pageSize: null,
        type: 0,
      }).then((resp) => {
        console.log("resp", resp);
        this.attributelist = resp.data.list;
        console.log("this.attributelist", this.attributelist);
      });
        //
      productSku(row.id, this.skukeyword).then((resp) => {
        console.log("SKUresp", resp);
        this.skudata = resp.data;
        this.skudata.forEach((element) => {
          element.spData = JSON.parse(element.spData);
          console.log("element.spData", element.spData);
          this.spData.push(element.spData);
        });
        console.log("this.skudata", this.skudata);
        console.log("this.spdata", this.spData);
      });
    },

    getslist() {
      getShoplist(this.queryparams)
        // SizeNum
        .then((resp) => {
          console.log("resp", resp);
          this.tableData = resp.data.list;
          this.total = resp.data.total;
        });
    },

    dProduct(id) {
      this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$message({
            type: "success",
            message: "!",
          }); //
          productDelete({
            deleteStatus: 1,
            ids: id,
          }).then(() => {
            this.getslist();
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },

    handleSizeChange(data) {
      this.queryparams.pageSize = data;
      // console.log(` ${data} `);
      this.getslist();
    },
    handleCurrentChange(data) {
      this.queryparams.pageNum = data;
      // console.log(`: ${data}`);
      this.getslist();
    },
    changeCateHandler(data) {
      // console.log(data);
      this.queryparams.productCategoryId = data[1];
    },
    seachHandler() {
      this.queryparams.pageSize = 5;
      this.queryparams.pageNum = 1;
      this.getslist();
    },
    resetSearch() {
      this.queryparams = {};
      this.getslist();
      this.$message("");
    },
    add() {
      this.$router.push("/shop/shopadd");
    },
    edit() {
      this.$router.push("/shop/shopedit");
    },
  },

  mounted() {
    this.getslist();
    //
    getPmsCateWithChildren().then((data) => {
      // console.log("", data);
      this.options = data.data;
    });

    //
    getBrandList().then((data) => {
      // console.log("branddata", data);
      this.brandList = data.data;
    });
  },
};
</script>

<style scoped='scoped'>
.bt_search_head {
  display: flex;
  justify-content: space-between;
  padding: 0px 20px;
  padding-top: 20px;
}
.el-form {
  padding: 20px 0px;
  margin-top: -20px;
}
.el-form .bt_form_br {
  display: flex;
  padding-left: 50px;
}
.el-form .el-form-item {
  flex: 1;
  padding: 0;
  margin: 0;
}
/* .fenye {
  float: left;
  margin-left: 55%;
} */
.gao {
  height: 10px;
}
.search {
  height: 175px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
}
.datalist {
  height: 60px;
  line-height: 60px;
  margin: 20px 20px;
  outline: 1px solid #dcdfe6;
  padding-left: 20px;
}
.dataliest-span2 {
  margin-left: 1290px;
}
.biao {
  margin: 20px 20px;
}
</style>
